ont: 'Inter', sans-serif;\n}","@use '../_vars.scss' as *;\n\n* {\n    top: 0;\n    margin: 0;\n    padding: 0;\n    font-family: var(--font);\n    box-sizing: border-box;\n}\n\n\nheader {\n    background-color: var(--black);\n}\n\n.navbar {\n    background: var(--black);\n    color: var(--white);\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    height: 70px;\n    position: fixed;\n    width: 100%;\n}\n\n.prioridad {\n    z-index: 1;\n}\n\n\n\na {\n    color: var(--white);\n    text-decoration: none;\n}\n\n.navbar__logo {\n    display: flex;\n    align-items: center;\n    padding: 0 20px;\n    justify-content: space-between;\n    cursor: pointer;\n}\n\n.navbar__logo img {\n    width: 50px;\n    border-radius: 50%;\n    margin-right: 10px;\n}\n\n.navbar__logo span {\n    font-size: 23px;\n    margin-right: 10px;\n}\n\n.navbar__menucategories .navbar-toggler {\n    background-color: var(--black);\n    border-radius: 20%;\n    height: 35px;\n    width: 40px;\n    padding: 0px;\n}\n\n.container-fluid {\n    display: flex;\n}\n\n.container-fluid-II {\n    display: flex;\n}\n\n.iconmenu {\n    color: var(--white);\n    font-size: 30px;\n}\n\n.navbar__menucategory-name_Container {\n    display: flex;\n    justify-content: center;\n    text-align: center;\n}\n\n.navbar__menucategory-name {\n    font-size: 18px;\n    padding: 2.8px 5px 5px 1px;\n}\n\n.offcanvas {\n    background-color: var(--black);\n    color: var(--white);\n    font-size: 1.2rem;\n}\n\n.navbar-nav .nav-link.active,\n.navbar-nav .show>.nav-link {\n    color: var(--white);\n    font-weight: 600;\n    text-align: center;\n}\n\n.navbar-nav a {\n    color: var(--white);\n}\n\n.btn-close-white {\n    filter: invert(1) grayscale(100%) brightness(200%);\n}\n\n.navbar__menucategories li a:hover {\n    background: var(--logo-red);\n    color: var(--white);\n}\n\n\n.navbar__search {\n    display: flex;\n    width: 40vw;\n    background-color: var(--white);\n    height: 40px;\n    align-items: center;\n    border-radius: 40px;\n}\n\n.navbar__search input {\n    border: none;\n    outline: none;\n    height: 35px;\n    font-size: 14px;\n    width: 36.4vw;\n    margin-left: 11px;\n}\n\n.seach_icon {\n    font-size: 26px;\n    color: var(--black);\n    padding-left: 3px;\n}\n\n.seach_icon:hover {\n    cursor: pointer;\n}\n\n.navbar__login {\n    margin-left: 23px;\n    font-size: 18px;\n    cursor: pointer;\n    display: flex;\n}\n\n/* AUI TERMINA EL SEARCH----------- */\n\n.login-icon {\n    font-size: 35px;\n    /* border: solid white; */\n}\n\n.text-login {\n    margin-left: 13px;\n    margin-top: 3px;\n    line-height: 22px;\n\n}\n\n.navbar__shop {\n    margin-left: 23px;\n    display: flex;\n}\n\n.navbar__shop a {\n    font-size: 27px;\n}\n\n.navbar__shop div {\n    background: var(--logo-red);\n    font-size: 18px;\n    border-radius: 50%;\n    height: 30px;\n    width: 30px;\n    padding: 2px 6px;\n    position: fixed;\n    top: 5px;\n    margin-left: 16px;\n    text-align: center;\n}\n\n@media screen and (max-width:1220px) {\n    header {\n        max-width: 100%;\n    }\n}\n\n@media screen and (max-width:900px) {\n\n    .navbar__menucategory-name {\n        display: none;\n    }\n\n    .navbar__login a {\n        display: none;\n    }\n\n    .navbar__search input {\n        width: 34vw;\n    }\n\n    .navbar__shop {\n        margin-left: 2px;\n    }\n}\n\n@media screen and (max-width:700px) {\n\n    .navbar {\n        align-items: center;\n        height: 130px;\n    }\n\n    .navbar__search {\n        position: absolute;\n        width: 85%;\n        top: 78px;\n    }\n\n    .navbar__search input {\n        box-sizing: border-box;\n        height: 35px;\n        margin-left: 13px;\n    }\n\n    #search {\n        width: 280px;\n    }\n\n    .seach_icon {\n        position: absolute;\n        margin-left: 90%;\n        font-size: 25px;\n    }\n\n    .navbar__logo {\n        padding: 0 2px;\n        margin-bottom: 44px;\n    }\n\n    .navbar__menucategories {\n        margin-bottom: 44px;\n        text-align: center;\n    }\n\n    .navbar__login {\n        margin-left: 2px;\n        margin-bottom: 44px;\n    }\n\n    .navbar__shop {\n        margin-left: 2px;\n        margin-bottom: 44px;\n    }\n\n    .navbar__shop div {\n        top: 11px;\n        font-size: 16px;\n        padding: 2px 4px;\n    }\n}\n\n@media screen and (max-width:390px) {\n    .navbar__logo img {\n        width: 45px;\n        border-radius: 50%;\n        margin-right: 5px;\n    }\n\n    #search {\n        width: 265px;\n    }\n\n    .seach_icon {\n        margin-left: 88%;\n        font-size: 24px;\n    }\n\n}\n\n@media screen and (max-width:354px) {\n    .navbar__logo span {\n        font-size: 19px;\n        margin-right: 7px;\n    }\n\n    .container-fluid {\n        padding: 0 5px 0 1px;\n    }\n\n    #search {\n        width: 250px;\n    }\n}\n\n@media screen and (max-width:320px) {\n    .navbar__logo img {\n        width: 39px;\n        border-radius: 50%;\n        margin-right: 10px;\n    }\n\n    #search {\n        width: 220px;\n    }\n}"],"sourceRoot":""}]);
