 background: rgb(235, 235, 235);\n    height: 100%;\n}\n\n.product-basquet{\n    display: flex;\n    border: 2px solid rgb(235, 235, 235);\n    border-radius: 10px;\n    max-width: 800px;\n    gap: 60px;\n    margin-bottom: 20px;\n    background: var(--white);\n    align-items: flex-start;\n}\n\n.product-image img {\n    width: 250px;\n    padding: 0 10px;\n}\n\n.product-description {\n    display: flex;\n    flex-direction: column;\n    width: 400px;\n    margin-top: 45px;\n}\n\n.product-name {\n    color: var(--very-light-gray);\n    font-weight: bold;\n    margin-bottom: 10px;\n}\n\n.product-info {\n    text-align: justify;\n    margin-bottom: 15px;\n}\n\n.product-price {\n    color: var(--logo-red);\n    font-size: 23px;\n}\n\n.shop-quantity{\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    margin-right: 20px;\n    margin-top: 45px;\n}\n\n .product-quantity {\n    display: flex;\n    gap: 12px;\n    height: 23px;\n    align-items: center;\n} \n\n .icon-remove,.icon-add {\n    background: rgb(235, 235, 235);\n    height: 24px;\n    color: var(--black);\n    padding: 6px 8px;\n    border-radius: 5px;\n    cursor: pointer;\n} \n\n.cont {\n    font-size: 20px;\n}\n\n.product-remove{\n    margin-top: 100px;\n}\n.product-remove button{\n    border: none;\n    background: #E0A100;\n    color: var(--white);\n    font-size: 13px;\n    font-weight: bold;\n    width: 100px;\n    padding: 10px;\n    border-radius: 16px;\n}\n\n.product-remove button i{\n    margin-right: 10px;\n}\n\n.container-total{\n    border: 2px solid rgb(235, 235, 235);\n    border-radius: 10px;\n    padding: 35px 50px;\n    display: flex;\n    flex-direction: column;\n    max-width: 500px;\n    height: 150px;\n    justify-content: space-around;\n    background: var(--white);\n}\n\n.quantity-total, .total-price{\n    display: flex;\n    justify-content: space-between;\n    margin-bottom: 15px;\n    gap: 20px;\n}\n\n.button-continue-shop{\n    align-self: center;\n}\n.