ont-size: 14px;\n    line-height: 17px;\n    padding-left: 0px;\n}\n\n.recoverin-password-text-input {\n    margin-top: 0px;\n    border: none;\n    font-weight: 400;\n    font-size: 14px;\n    line-height: 17px;\n    width: 85%;\n    border-bottom: 1px solid #ACAFAE;\n    padding-bottom: 10px;\n}\n\n.recoverin-password-button {\n    width: 312px;\n    height: 48px;\n    font-weight: 400;\n    font-size: 18px;\n    line-height: 22px;\n    color: var(--white);\n    background: var(--logo-red);\n    border-radius: 22px;\n    border: none;\n    align-self: center;\n    margin-top: 28px;\n    margin-bottom: 14px;\n}\n\n.recovering-password-link {\n    display: flex;\n    justify-content: center;\n}\n\n.recovering-password-link a {\n    font-size: 14px;\n    line-height: 17px;\n    color: var(--logo-red);\n    margin-top: 16px;\n}\n\n\n@media screen and (max-width:1370px) {\n    .recovering-password-container {\n        height: 100%;\n    }\n\n    .recoverin-password-container-of-container {\n        height: 100%;\n    }\n\n    .recovering-password-image svg {\n        width: 70%;\n    }\n\n    .recovering-password-header {\n        margin-top: 35px;\n    }\n\n}\n\n\n@media screen and (max-width:800px) {\n    .recoverin-password-container-of-container {\n        height: 94vh;\n        background: var(--white);\n        display: block;\n    }\n\n    .recovering-password-image {\n        margin-top: 30px;\n        width: 100%;\n        height: 25%;\n    }\n\n    .recovering-password-header {\n        margin-bottom: 10px;\n    }\n\n    .recoverin-password-container-content {\n        width: 100%;\n        height: 65%;\n    }\n\n    .recovering-password-image svg {\n        height: 90%;\n    }\n\n    .recoverin-password-image-letter svg {\n        width: 120px;\n        height: 120px;\n    }\n\n    .recoverin-password-form {\n        margin-top: 10px;\n    }\n\n}\n\n@media screen and (max-width:500px) {\n\n    .recoverin-password-container-of-container {\n        height: 100%;\n        margin-top: 18%;\n        background: var(--white);\n    }\n\n    .recovering-password-image {\n        display: none;\n    }\n\n\n    .recoverin-password-container-content {\n        width: 100%;\n        height: 100%;\n    }\n\n    .recovering-password-header {\n        margin-top: 10px;\n    }\n\n    .recoverin-password-image-letter svg {\n        width: 110px;\n        height: 110px;\n    }\n\n    .recoverin-password-text-label-correo {\n        margin-top: 20px;\n        margin-bottom: 3px;\n    }\n\n    .recoverin-password-button {\n        margin-top: 40px;\n        width: 270px;\n    }\n\n}\n\n@media screen and (max-width:355px) {\n    .recoverin-password-container-of-container {\n        margin-top: 20%;\n    }\n\n    .recoverin-password-info {\n        width: 250px;\n        margin: 0 auto;\n    }\n\n    .recoverin-password-text-label-correo {\n        margin: 17px auto;\n    }\n\n    .recoverin-password-image-letter svg {\n        width: 95px;\n        height: 95px;\n    }\n\n    .recoverin-password-text-input {\n        margin: 0 auto;\n        width: 85%;\n    }\n\n    .recoverin-password-button {\n        width: 220px;\n    }\n}\n\n@media screen and (max-width:330px) {\n    .recovering_password_container_new_contrasea {\n        margin-top: 3%;\n    }\n\n    .recovering_password_container_confirm_contrasea {\n        margin-top: 3%;\n        margin-bottom: -25px;\n    }\n}",":root {\n    /* colors*/\n    --white: #FFFFFF;\n    --black: #000000;\n    --dark-red: #720007;\n    --logo-red: #C00100;\n    --very-light-gray: #ADA4A4;\n    --text-input-field: #f1f1f1;\n    --gold: #D9AC2A;\n    /* COLORES AADIDOS */\n    --orange:#F25C05;\n    --light-orange:#F28705;\n    --light-gray: #F5F5F5;\n\n    /*size*/\n    --sm: 14px;\n    --md: 16px;\n    --lg: 18px;\n\n    /*font*/\n    --font: 'Inter', sans-serif;\n}"],"sourceRoot":""}]);
