import React from 'react';
import useGetProducts from '../../hooks/useGetProducts';
import ItemProducts from './ItemProducts';
import { useNavigate } from 'react-router-dom';

import '../table.css';

const Tableproducts = () => {
    //Traemos la data de sliders
    const APIProducts = 'https://api-empresaqb-version3-production.up.railway.app/api/products';
    const productsdata = useGetProducts(APIProducts);

    const navigate = useNavigate();

    const handleOnAadirProducto = () => {
        navigate('/anadirproductos')
    }

    return (
        <>
        <div className='container-boton-aadir'>
        <button
            className='boton-aadir'
                onClick={() => handleOnAadirProducto()}
            >
                Aadir productos
            </button>
        </div>
            <div className='container-table'>
                <table>
                    <thead>
                        <tr>
                            <th>ID</th>
                            <th>Producto</th>
                            <th>Precio Unitario</th>
                            <th>Precio por Docena</th>
                            <th>Descripcin</th>
                            <th>image</th>
                            <th>cantidad</th>
                            <th>CategoriaId</th>
                            <th>Acciones</th>
                        </tr>
                    </thead>
                    <tbody>
                        {productsdata.map((productitem) => (
                            <ItemProducts productitem={productitem} key={productitem.id} />
                        ))}
                    </tbody>
                </table>
            </div>
        </>

    );
}

export default Tableproducts;